  width: 90px;
  height: 18px;
  background: linear-gradient(#36cef9, #037aff);;
  border-radius: 3px;
  filter: blur(13,04px);
}

.tm-text-scaner {
  color: #00f652;
  margin: 0;
}

.tm-text-subscaner {
  color: rgba(64,98,128,0.5);

}

.tm-button-start {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 110px;
  height: 110px;
  background-color: transparent;
