  //  font-size: 40px;
  //  //color: #02c49f;
  //  color: rgba(2,196,159,0.55);
  //}

  &:active, &:focus, &:hover {
    //border-color: #979797;
    box-shadow: 0 0 22px 0 rgba(2,196,159,0.92);
  }

  //&:before {
  //  position: absolute;
  //  display: block;
  //  width: 476px;
  //  height: 476px;
  //  top: -170%;
  //  left: -170%;
  //  background: url("../assets/images/btn-power-circles.png") no-repeat;
  //  //background-size: contain;
  //  content: "";
  //  transform: rotate(-5deg);
  //  z-index: -1;
  //}
}

.tm-scale-header-alt {
  position: relative;
  padding-bottom: 90px;
  color: #02c49f;

  &::before {
    content: '';
    display: block;
    position: absolute;
    bottom: 25px;
    left: 50%;
    width: 2px;
    height: 48px;
    background-color: rgba(2,196,159,0.2);
