@media (max-width: 959px) {
  html {
    background: #ffffff;
  }

  .tm-content-header {
    border-bottom-color: transparent;
  }

  .tm-progressbar-line___outer {
    height: 1px;
    background-color: #a0dade;
  }

  .tm-progressbar-line__inner {
    height: 3px;
    background-color: #19bb4f;
  }

  .tm-main {
    padding: 0;

  }

  .tm-content-wrapper {
    margin: 0 auto;
  }


  body {
    min-width: 300px;
  }

  //exp
  .tm-start-only {
    padding-bottom: 120px;
  }
}

.tm-padding-s {
  padding: 30px 0 0;
}

@media (max-width: 639px) {

  .tm-content-wrapper .tm-start-only {
    overflow: hidden;
  }

  .tm-start-only {
    background: $global-background;
    color: #fff;
    font-size: $font-size-small;
    padding-bottom: 0;

    .tm-text-scaner {
      color: #fff;
      font-size: $font-size-h2;
    }

    .tm-text-subscaner {
      color: #fff;
      font-size: 10px;
    }

    .tm-scale-header-alt {
      color: #fff;
      font-size: $font-size-tiny;

      &:before {
        background-color: rgba(98,221,80,0.5);
      }
    }

    .tm-button-start {
      border: 1px solid transparent;
      box-shadow: 0 0 27px 0 rgba(98,221,80,0.55);

      &:active, &:focus, &:hover {
        box-shadow: 0 0 22px 0 rgba(98,221,80,0.92);
      }
    }

    .tm-button-start-wrapper:before {
      background-color: #19bb4f;
      background-blend-mode: multiply;
    }

    .tm-links-alt {

      div {
        padding: 5px 0;
      }

      a {
        color: #fff;
      }
    }

    .tm-sidebar-title {
      padding: 50px 0 10px;
    }
  }

  .tm-answer {
    font-size: $font-size-small;
  }

  .uk-logo {
    font-size:$font-size-small;
  }

  .tm-content-header {
    padding: 20px 0;
  }

  .tm-button-continue {
    margin-bottom: 0 !important;
  }

  h1 {
    font-size: $font-size-small;
  }

  h2 {
    font-size: $font-size-global;
    margin: 0 0 10px 0;
  }

  .uk-text-large {
    font-size: $font-size-global;
  }

  .uk-accordion-content {
    font-size: $font-size-small;
    border-top: 1px solid #c1cdd9;
  }

  .tm-fact {
    padding: 30px 0 0;
  }

  .tm-padding-s {
    //padding: 30px 0 0;
    width: 100%;

    button {
      width: 100%;
    }
  }

  .tm-button-next {
    margin-bottom: 10px;
  }

  .tm-font-color-alt-s {
    color: #628af7;
  }
  .tm-cover-card {
    //background-size: cover;
    //min-height: 240px;
    //border-radius: 2px;
    //background-repeat: no-repeat;
    //background-position: center right;
  }

  .tm-diagnostics-card, .tm-budget-card, .tm-reserve-card, .tm-insure-card, .tm-invest-card {
    background-color: #19bb4f;
    background-image: none;
    padding-bottom: 0;
  }
}

@media (min-width: 960px) and (max-width: 1139px) {
  .tm-summary {
    padding: 20px 25px 20px 0;
  }
}

@media (min-width: 1140px) {
  .tm-main {
    padding-right: 334px;

    .tm-content-wrapper {
      margin: 0 auto;
    }

    &.tm-summary {
      padding-right: 134px;
    }
  }

}

.tm-button-start-wrapper {
  position: relative;
  &:before {
    position: absolute;
    display: block;
    width: 476px;
    height: 476px;
    top: -166%;
    left: -166%;
    background: url("../assets/images/btn-power-circles.png") no-repeat;
    //background-size: contain;
    content: "";
    transform: rotate(-5deg);
    z-index: -1;
  }
}

.tm-margin-megalarge {
  margin-top: 110px;
}

.tm-bottom-panel {
  background-color: #fff;
  border-top: 1px solid #eee;
  padding-top: 10px;
}




//Summary
.tm-summary {
  font-size: $font-size-small;
  font-weight: $font-weight-thin;

  .tm-text-transparent {
    color: rgba(34, 41, 76, 0.5);
  }

  h1 {
    font-family: $font-family-global;
    font-size: 26px;
    font-weight: $font-weight-thick;
    color: $font-color-global;
    margin: 10px 0 20px;
  }

  h2 {
    font-family: $font-family-global;
    font-size: 20px;
    font-weight: $font-weight-thick;
    color: $font-color-global;
    margin: 0 0 10px 0;
  }

  .tm-button-details {
    color: $font-color-global;
    border: 1px solid #9094a5;
    font-weight: $font-weight-thin;
    border-radius: 3px;

    &:hover, &:focus, &:active {
      color: darken($font-color-global, 30%);
      border-color: darken(#9094a5, 30%);
    }
  }

  .tm-icon-warning {
    position: relative;
    padding-left: 25px;
    margin-top: 20px;

    color: rgba(34, 41, 76, 0.5);

    &:before, &:after {
      position: absolute;
      display: block;
    }

    &:before {
      top: 2px;
      left: 8px;
      font-family: FontAwesome;
      font-size: 12px;
      color: #EB5A80;
      content: '\F12A';
    }

    &:after {
      top: 0;
      left: 0;

      width: 16px;
      height: 16px;
      border: 2px solid #EB5A80;
      border-radius: 50%;
      content: '';
    }
  }

  .tm-button-order {
    position: relative;
    //padding: 0 30px;
    width: 100%;
    min-width: 290px;
    font-weight: $font-weight-thick;
    border-radius: 500px;

    .tm-button-icon {
      position: absolute;
      display: block;
      top: 40%;
      right: 10px;

    }
  }

  .tm-header-divider {
    position: relative;
    left: -15px;
    width: 110%;
    height: 2px;
    background-color: $global-background;
    margin-top: 10px;
  }

  .tm-small-progressbar {
    width: 136px;
    height: 136px;

    .CircularProgressbar .CircularProgressbar-trail {
      stroke: #cbfff5;
      stroke-width: 8px;
    }

    .CircularProgressbar .CircularProgressbar-path {
      stroke: #4DB4D0;
      stroke-width: 8px;
    }

    .tm-indicator__content span {
      font-size: 28px;
      font-weight: 700;
    }
  }

  input {
    color: rgba(34, 41, 76, 0.5);
  }

  @media (min-width: 960px) and (max-width: 1080px) {
    .tm-button-details {
      padding: 0 5px;
    }

    .tm-button-order {
      min-width: 250px;
      padding: 0 10px;

      .svg-wrapper {
        display: none;
      }
    }
  }

  @media (max-width: 639px) {
    h1 {
      font-size: 20px;
    }
  }

  @media (max-width: 350px) {
    .tm-button-order {
      min-width: 240px;
      padding: 0;
      font-size: 12px;

      .tm-button-icon {
        display: none;
      }

    }
  }
}


// Modals
.uk-modal-dialog {
  opacity: 1;
}

.uk-modal-close {
  padding: 0 50px;
  font-weight: $font-weight-thick;
  border-radius: 500px;
}

.tm-modal {
  font-size: $font-size-small;
  font-weight: $font-weight-thin;
  padding: 40px;
  background-color: #ffffff;
  box-shadow: 0 12px 15px 0 rgba(0, 0, 0, 0.25);
  margin: auto;
  border-radius: 20px;

  h2 {
    font-family: $font-family-global;
    font-size: 20px;
    font-weight: $font-weight-thick;
    color: $font-color-global;
    margin: 0 0 10px 0;
  }

  p {
    color: #9094A5;
  }
}

.tm-modal-button-close {
  font-weight: $font-weight-thick;
  color: #6b6f73;
  border-radius: 500px;

  &:hover, &:focus, &:active {
    color: darken(#6b6f73, 30%);
  }
}

.tm-modal-close {
  cursor: pointer;
  fill: #6b6f73;

  &:hover {
    fill: darken(#6b6f73, 30%);
  }
}

.tm-modal-small {
  max-width: 420px;
  padding: 0 40px;
}

.tm-modal-description {
  max-width: 600px;

  .tm-modal-link {
    position: relative;
    color: $font-color-primary;
    padding: 0 7px;

    &:before, &:after {
      position: absolute;
      display: block;
      top: -1px;
    }

    &:before {
      content: '\00ab';
      left: 0;
    }

    &:after {
      content: '\00bb';
      right: 0;
    }
  }
}

.tm-modal-controls {
  button {
    min-width: 185px;
  }
}

@media (max-width: 639px) {
  .tm-modal {
    padding: 20px;
  }
}
